import React from "react";
import { Link } from "react-router-dom";
import {
  Col,
  Collapse,
  Container,
  Nav,
  Navbar,
  NavbarBrand,
  NavbarToggler,
  NavItem,
  NavLink,
  Row,
} from "reactstrap";

// Logo
import Logo from "../../assets/img/logo.png";
const Header = (props) => (
  <div className="Header">
    <div className="top_bar">
      <Container fluid>
        <Row>
          <Col lg="6">
            <ul className="top_menu">
              <li>
                <a href="tel:+918385842313">
                  <i className="fas fa-phone" />
                  +91-8385842313
                </a>
              </li>
              <li>
                <a href="mailto:info@pujyapanditg.com">
                  <i className="fas fa-envelope" /> info@pujyapanditg.com
                </a>
              </li>
            </ul>
          </Col>
          <Col lg="6"></Col>
        </Row>
      </Container>
    </div>
    <Navbar color="light" expand="md" light>
      <NavbarBrand to="/" tag={Link}>
        <img
          src={Logo}
          style={{
            width: "200px",
            position: "absolute",
            top: "5px",
            zIndex: "9",
          }}
          alt="Logo"
        />
      </NavbarBrand>
      <NavbarToggler onClick={function noRefCheck() {}} />
      <Collapse navbar>
        <Nav className="ml-auto" navbar>
          <NavItem className="active">
            <NavLink to="/" tag={Link}>
              Home
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="">About</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="">Pooja Booking</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="" className="btn btn-outline-amber btn-rounded px-4">
              Register
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              href=""
              className="btn btn-amber btn-rounded px-4 text-white"
            >
              Login
            </NavLink>
          </NavItem>
        </Nav>
      </Collapse>
    </Navbar>
  </div>
);
export default Header;
