import React from "react";
import { Link } from "react-router-dom";

//bootstrap
import { Button, Card, CardBody, CardTitle } from "reactstrap";

// react-router-dom
// import { Link } from "react-router-dom";
const PoojaCards = ({ data }) => (
  <Card className="product_cards">
    <div className="img_wraped">
      <img
        className="img-fluid rounded"
        src={data.image}
        alt={data.title}
        loading="lazy"
      />
    </div>

    <CardBody>
      <CardTitle className="text-truncate">{data.name}</CardTitle>
      {data.package_type === "single" && (
        <>
          <h5>Rs. {data.price}</h5>
          <h6 className="small">Samagri Price Rs. {data.samagri_price}</h6>
        </>
      )}
      <div className="action">
        <Button
          className="btn-white btn-rounded"
          to={`/pooja/${data._id}`}
          tag={Link}
        >
          View More
        </Button>
      </div>
    </CardBody>
  </Card>
);
export default PoojaCards;
